.AnswerItem {
 border: 1px solid #fff;
  border-radius: 5px;
  padding: 5px 10px;
  margin-bottom: 5px;
  cursor: pointer;
}

@media screen and (min-width: 960px){
  .AnswerItem:hover{
    background: rgba(255,255,255, .2);
    transition: background-color .3s ease-in-out;
  }
}

.AnswerItem.error{
  background: rgba(240,87,108, .7);
}
.AnswerItem.success{
  background: rgba(161,240,69, .7);
}