.ActiveQuiz {
  box-sizing: border-box;
  padding: 20px;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 5px;
}

.ActiveQuiz span {
  margin-right: 10px;
}
.ActiveQuiz small {
  white-space: nowrap;
}

.Question {
  display: flex;
  justify-content: space-between;
  margin-top: 0;
}